export const saveBtn = {
  data: function() {
    return {};
  },
  methods: {
    saveAction(btnIcon, btnColor, event = true) {
      const originBtnIcon = this[btnIcon];
      const originBtnColor = this[btnColor];

      if (event) {
        this[btnIcon] = 'done';
        this[btnColor] = 'positive';
      } else {
        this[btnIcon] = 'report_problem';
        this[btnColor] = 'red';
      }
      setTimeout(() => {
        this[btnIcon] = originBtnIcon;
        this[btnColor] = originBtnColor;
      }, 2000);
    }
  }
};
