<template>
  <div class="html-to-txt">
    <div v-if="folderName">
      <q-btn
        class="full-width"
        :color="btnColorConvert"
        @click="convert"
        label="Конвертировать HTML в TXT"
        :icon-right="btnIconConvert"
        :loading="isProcess"
        :disable="isProcess"
      >
        <template v-slot:loading>
          <q-spinner-tail size="sm" />
        </template>
      </q-btn>
    </div>
    <div v-else>
      <h6 class="no-file">Файл не выбран</h6>
    </div>
  </div>
</template>

 <script>
import { mapState } from 'vuex';
import { saveBtn } from '@/mixins/saving';

export default {
  name: 'HTMLtoTXT',
  mixins: [saveBtn],
  data: function () {
    return {
      btnColorConvert: 'primary',
      btnIconConvert: 'settings_ethernet',
      isProcess: false,
    };
  },
  computed: {
    ...mapState({
      baseURL: (state) => state.baseURL,
      folderName: (state) => state.folderName,
    }),
    redirectAfterFiles: {
      get() {
        return this.$store.state.redirectAfterFiles;
      },
      set(value) {
        this.$store.commit('setRedirectAfterFiles', value);
      },
    },
  },
  watch: {},
  methods: {
    async convert() {
      this.isProcess = true;

      let response, result;
      try {
        response = await fetch(this.baseURL + 'create_txt', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json;charset=utf-8',
          },
          body: JSON.stringify({ folderName: this.folderName }),
        });
        result = await response.json();

        if (result.error !== '') {
          this.saveAction('btnIconConvert', 'btnColorConvert', false); //mixin
          console.error(result.error);
        } else {
          this.saveAction('btnIconConvert', 'btnColorConvert'); //mixin
        }
      } catch (error) {
        this.saveAction('btnIconConvert', 'btnColorConvert', false); //mixin
        console.error(error);
      }

      this.isProcess = false;
    },
  },
  mounted() {
    this.redirectAfterFiles = '/htmltotext';
  },
  created() {},
};
</script>

 <style  lang="scss" scoped>
</style>